"use client";

import { ComparisonSlider } from "@/components/ui/comparison-slider";
import { ComparisonImage } from "@/components/ui/comparison-image";

export default function BeforeAfterSlider({ before, after }) {
  return (
    <div className="w-full h-80 md:h-96 rounded-3xl overflow-hidden bg-muted">
      <ComparisonSlider
        className="h-full"
        defaultValue={50}
        thumbClassName="
          bg-card border border-border shadow 
          w-10 h-10 rounded-full
          flex items-center justify-center text-foreground
        "
      >
        {/* BEFORE IMAGE */}
        <ComparisonImage
          src={before}
          alt="Before"
          className="object-cover"
        />

        {/* AFTER IMAGE */}
        <ComparisonImage
          src={after}
          alt="After"
          className="object-cover"
        />
      </ComparisonSlider>
    </div>
  );
}
